import React, { Component } from 'react';
import { List } from 'antd';
import { Link } from "react-router-dom";
import { myblogData, gojsData } from "../../assets/data";
import "./index.less";

export default class index extends Component {
    constructor(props) {
        super(props);
        this.state = {
            dataSource: []
        };
    }

    componentDidMount() {
        let dataSource = this.state.dataSource;
        dataSource.push({
            href: myblogData.baseHref,
            className: myblogData.name,
            title: myblogData.title,
            description: myblogData.description,
            img: myblogData.img,
            content: myblogData.content,
        }, {
            href: gojsData.baseHref,
            className: gojsData.name,
            title: gojsData.title,
            description: gojsData.description,
            img: gojsData.img,
            content: gojsData.content,
        });
        this.setState({
            dataSource
        });
    }
    render() {
        return (
            <List
                itemLayout="vertical"
                size="large"
                dataSource={this.state.dataSource}
                renderItem={item => (
                    <Link to={item.href}>
                        <List.Item
                            className="list-item"
                            key={item.title}
                            extra={
                                <div className="box">
                                    <img src={require("../../assets/images/" + item.img)} alt={item.title} />
                                    <div className="box-content">
                                        <span className="title">点击进入</span>
                                    </div>
                                </div >

                            }
                        >
                            <List.Item.Meta
                                title={item.title}
                                description={item.description}
                            />
                            <p style={{ textIndent: "2em", color: "rgba(0, 0, 0, 0.65)" }}>{item.content}</p>
                        </List.Item>
                    </ Link>
                )}
            />
        )
    }
}
