import React, { Component } from 'react'
import { Breadcrumb } from 'antd';
import { Link, withRouter } from "react-router-dom";
import { myblogData,gojsData } from "../../assets/data";
import "./index.less";

const breadcrumbNameMap = {
    [myblogData.baseHref]: myblogData.title,
    [gojsData.baseHref]: gojsData.title,
};
for (let i = 0; i < gojsData.catalog.length; i++) {
    breadcrumbNameMap[gojsData.catalog[i].href] = [gojsData.catalog[i].title]
}

class index extends Component {
    render() {
        const pathSnippets = this.props.location.pathname.split('/').filter(i => i);
        const extraBreadcrumbItems = pathSnippets.map((_, index) => {
            const url = `/${pathSnippets.slice(0, index + 1).join('/')}`;
            return (
                <Breadcrumb.Item key={url}>
                    <Link to={url}>{breadcrumbNameMap[url]}</Link>
                </Breadcrumb.Item>
            );
        });
        return (
            <Breadcrumb>
                {<>
                    <Breadcrumb.Item key="home">
                        <Link to="/">首页</Link>
                    </Breadcrumb.Item>
                    {extraBreadcrumbItems}
                </>}
            </Breadcrumb>
        )
    }
}

export default withRouter(index);